const quotesSlider = $ => {
	const owl = $('.quotes__slider-container');
	owl.owlCarousel({
		dots: false,
		items: 1
	});

	const leftBtn = $('quotes__nav-btn_left');
	const rightBtn = $('.quotes__nav-btn_right');
	leftBtn.click(() => {
		owl.trigger('prev.owl.carousel');
		this.blur();
	});

	rightBtn.click(() => {
		owl.trigger('next.owl.carousel');
		this.blur();
	});
};

module.exports = quotesSlider;



